@import '../../variables';

.admin_article {
    &__header {
        padding: 16px 22px;
        box-shadow:
            0px -0.5px 0px #E0E2E8,
            0px 0.5px 0px #E0E2E8;
        box-sizing: border-box  ;
        display: flex;
        justify-content: space-between;
    }

    &__filters {
        display: flex;
        justify-content: space-between;
    }

    &__main {
        padding: 14px 22px;
        box-sizing: border-box;
        flex-grow: 1;
    }

    &__footer {
        padding: 21px 22px;
        box-shadow:
            0px -0.5px 0px #E0E2E8,
            0px 0.5px 0px #E0E2E8;
        box-sizing: border-box;
    }

    &__filter_btns {
        .btn:not(:last-child) {
            margin-right: 15px;
        }
    }
}